import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import {Menu,MenuItem,Button} from '@material-ui/core';
import { Link } from "react-router-dom";
import "./../../../styles/landingPageAppbar.css"

const StyledMenu = withStyles({
  paper: {
    border: '1px solid #d3d4d5',
  },
})((props) => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'center',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center',
    }}
    {...props}
  />
));


export default function DropDownBtnResources({ id, name, click }) {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <div onClick={click}>
      <Button
        component={Link}
        className="link menu1"
        aria-controls="customized-menu"
        aria-haspopup="true"
        variant="text"
        onClick={handleClick}
        id={id}
        fullWidth
        to=""
      >
        Resources
      </Button>
      <StyledMenu
        id="customized-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
        <Link onClick={() => { click('/blog'); handleClose(); }} name="/blog" className="dropDownLinks" to='/'>
          <MenuItem>
            <span className="menuButtonOption">Blog</span>
          </MenuItem>
        </Link>
        <Link onClick={() => { click('/ebooks'); handleClose(); }} name="/ebooks" className="dropDownLinks" to='/'>
          <MenuItem>
            <span className="menuButtonOption">Ebooks</span>
          </MenuItem>
        </Link>
        <Link onClick={() => { click('/case-studies'); handleClose(); }} name="/case-studies" className="dropDownLinks" to='/'>
          <MenuItem>
            <span className="menuButtonOption">Case Studies</span>
          </MenuItem>
        </Link>
        <Link onClick={() => { click('/cost-estimator'); handleClose(); }} name="/cost-estimator" className="dropDownLinks" to='/'>
          <MenuItem>
            <span className="menuButtonOption">Cost Estimator</span>
          </MenuItem>
        </Link>
        <Link onClick={() => { click('/faq'); handleClose(); }} name="/faq" className="dropDownLinks" to='/'>
          <MenuItem>
            <span className="menuButtonOption">FAQ</span>
          </MenuItem>
        </Link>
      </StyledMenu>
    </div>
  );
}
