import React, { useEffect } from "react";
import { useAnimation, motion } from 'framer-motion/dist/framer-motion';
import inView, { useInView } from "react-intersection-observer";

import { Grid, Typography, Link } from '@material-ui/core'
import './../../../styles/landingPageHomeComponents.css'
import comp3 from './../../../images/HeartPump.gif'
const CordBlood = ({ device }) => {
    const { ref, inView } = useInView({ threshold: .2 });
    const animation = useAnimation();
    useEffect(() => {
        console.log("inview=", inView);
        if (inView) {
            animation.start({
                y: 0,
                scale: 1 ,
                opacity: 1,
                rotate : 0,
                transition: {
                    type: 'spring', duration: 1, bounce: 0.2
                }
            })
        } if (!inView) {
            animation.start({
                // x: '-100vw',
                // scale: 0 ,
                // rotate : -30,
                // opacity: 0,
            })
        }
    }, [inView]);
    return (
        <div ref={ref}>
            <motion.div
                initial={{ y: '100vw', opacity: 0, scale: 1 , rotate : 0, }}
                animate={animation}
                whileHover={{ scale: 1.02 }}>
                <Grid container justifyContent="center" className="pagePadding">
                    <Grid item xs={11}>
                        <Grid container justifyContent="space-between">
                            <Grid item xs={12} md={6} lg={6}>
                                <Typography className="headingNormal">Why Should You Bank Cord Blood?</Typography>
                                <Grid className="textMargin">
                                    <Typography className="textPadding" >STEM CELLS are found in cord blood, cord tissue, and placental tissue. These cells are highly valuable to baby, mom, and possibly other family members. When you save these stem cells with TIWIT.LIFE, you ensure that they are securely stored for you and your family’s future needs.</Typography>
                                </Grid>
                                <Grid container justifyContent="center">
                                    <Link to="" className="linkBtn"> KNOW MORE</Link>
                                </Grid>
                            </Grid>
                            <Grid item container xs={12} md={6} lg={5} >
                                <Grid container direction="row" align="center" justifyContent="center">
                                    <Grid item >
                                        <img src={comp3} alt="" width="120%" />
                                        {/* <img src={comp3} alt="" width={(device==="small")? "100%" : "85%"}/> */}
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </motion.div></div>
    )
}

export default CordBlood
