import React, { useState,useEffect } from 'react';
import { Grid, Typography, AppBar, Toolbar, Button } from '@material-ui/core';
import { Link } from 'react-router-dom';
import './../../../styles/landingPageAppbar.css';
import brandLogo from './../../../images/brandLogo.png'
import enroll from './../../../images/enroll.png'
import login from './../../../images/login.png'
import DropDownBtnServices from './dropDownBtnServices';
import DropDownBtnResources from './dropDownBtnResources';
const Appbar = (props) => {
    const [activeMenu, setActiveMenu] = useState(window.location.pathname);
    const [activeMenuResources, setActiveMenuResources] = useState(window.location.pathname);
    const [hideLoginButton, setHideLoginButton] = useState();

    useEffect(() => {
        window.addEventListener("resize", props.setDevice);
    })



    const handleClick = async(e) => {
        if (e !== undefined &&  e !== '') {
            setActiveMenuResources('/')
            setHideLoginButton('')
            setActiveMenu(e)
        }
    }
    const handleClickResources = (e) => {
        if (e === '/blog' || e === '/ebooks' || e === '/case-studies' || e === '/cost-estimator' || e === '/faq' ) {
            setActiveMenu('/')
            setHideLoginButton('')
            setActiveMenuResources('/resources')
        }
    }

    const hideLoginButtonFunc = () => {
        setHideLoginButton('/login')
        setActiveMenu('/')
        setActiveMenuResources('/')
    }
    if(props.footer.linkFooter === true && (activeMenu !== '/' || activeMenuResources !== '/'))
    {
        setActiveMenu('/')
        setActiveMenuResources('/')
        props.setFooterLink(false)

    }

    return (
        // ROY
        //<div style={{'margin-left':props.device==="small"?0:30,'margin-right':props.device==="small"?0:30}}>
        <Grid container justifyContent="center">

            <Grid item xs={11}>
                <AppBar position="static" color="transparent" elevation={0} className="appbar">
                    <Toolbar className={(props.device!=="large") && (window.location.pathname === "/login" || hideLoginButton === '/login') ? "toolBarSm" : "toolBar"}>
                        <Grid container justifyContent="space-between">

                            <Grid item xs={12} md={4} lg={3}  style={{display:"flex",alignItems:"center"}}>
                             
                                <a href="/" ><img src={brandLogo} alt="Tiwit Logo" width={(props.device==="small" ? "85%":"100%").toString()} /></a>
                            
                            </Grid>
                            <Grid item container xs={12} md={8} lg={8} alignItems="center" className={(props.device==="small") ? "menuItemSm" : "menuItem"} >
                                    <Grid item container alignItems="center" justifyContent={ (props.device==="small")&& (window.location.pathname !== "/login" && hideLoginButton !== '/login') ? "center": "flex-end"}>
                                        <Grid item >
                                       <Link   className={props.device==="small" ? "callLinkSm" : "callLink"} >1800 - 8444 - 94448</Link>
                                    </Grid>
                                    {window.location.pathname === "/login" || hideLoginButton === '/login' ? null :
                                        < >
                                           <Grid item className={(props.device==="small") ? "BgClr1Sm" : "BgClr1"} align="center">
                                           <Link onClick={hideLoginButtonFunc} className="anchor" to="/login" >
                                                <img src={enroll} alt="Enroll" height="45px" />
                                                <Typography variant="body2" align="center" className="appbarText">ENROLL</Typography>
                                            </Link></Grid>
                                            <Grid item className="BgClr2 marginLeft11" align="center" >
                                            <Link onClick={hideLoginButtonFunc} className="anchor" to="/login" >
                                                <img src={login} alt="Login" height="45px" />
                                                <Typography variant="body2" align="center" className="appbarText" >LOGIN</Typography>
                                                </Link>
                                            </Grid>
                                        </>
                                    }
                            </Grid>
                            </Grid>
                        </Grid>
                    </Toolbar>
                </AppBar>
                <Grid container justifyContent="center" className="menus">
                    <Grid item xs={12} md={10} lg={9}>
                        <Grid container justifyContent="space-around" alignItems="center" alignContent="center" align="center">
                            <Grid item xs={6} md={2} lg={2}>
                                <Button
                                    component={Link}
                                    className="link menu1"
                                    to="/why-us"
                                    name="/why-us"
                                    onClick={()=>handleClick("/why-us")}
                                    id={activeMenu === "/why-us" ? "active" : ""}
                                    fullWidth
                                >
                                    Why Us
                                </Button>
                            </Grid>
                            <Grid item xs={6} md={2} lg={2}>
                                <DropDownBtnServices
                                    customStyle="link menu1"
                                    id={(activeMenu === "/pregnancy-consulting") ||
                                        activeMenu === "/genome-mapping" ? "active" : ""}
                                    click={handleClick}
                                    link1={'/'}
                                    link2={'/genome-mapping'}
                                    link3={'/pregnancy-consulting'}
                                />
                            </Grid>
                            <Grid item xs={6} md={2} lg={2}>
                                <DropDownBtnResources
                                    customStyle="link menu1"
                                    id={(activeMenuResources === "/resources") ? "active" : ""}
                                    click={handleClickResources}
                                />
                            </Grid>
                            <Grid item xs={6} md={2} lg={2}>
                                <Button
                                    className="link menu1"
                                    component={Link}
                                    to="/contact"
                                    name="/contact"
                                    onClick={()=>handleClick("/contact")}
                                    id={activeMenu === "/contact" ? "active" : ""}
                                    fullWidth
                                >
                                    Contact
                                </Button>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
        //</div>
    )
}

export default Appbar
