import React, { useEffect } from "react";
import { useAnimation, motion } from 'framer-motion/dist/framer-motion';
import inView, { useInView } from "react-intersection-observer";

import {Grid,Typography} from '@material-ui/core'
import './../../../styles/landingPageHomeComponents.css'
import YoutubeModal from '../../UI/youtubeModal';

const GynecDoctor = ({device}) => {
    const { ref, inView } = useInView( {threshold:.2});
    const animation = useAnimation();
    useEffect(() => {
        console.log("inview=", inView);
        if (inView) {
            animation.start({
                y: 0,
                scale: 1 ,
                opacity: 1,
                rotate : 0,
                transition: {
                    type: 'spring', duration: 1, bounce: 0.2
                }
            })
        } if (!inView) {
            animation.start({
                // x: '100vw',
                // scale: 0 ,
                // opacity: 0,
                // rotate : 30,
            })
        }
    }, [inView]);
    
    return (
        <>
            <div ref={ref}>
            <motion.div
                initial={{ y: '100vw', opacity: 0, scale: 1 , rotate : 0, }}
                animate={animation}
                whileHover={{ scale: 1.02 }}>
        <Grid container justifyContent="center" className="pagePadding">
            <Grid item xs={11}>
            <Grid container justifyContent="space-between">
                    <Grid item xs={12} md={6} lg={6}>
                        <Typography variant="h5" className="headingNormal">What Your Gynec Doctor Says?</Typography>
                        <Grid className="textMargin">
                        <Typography variant="body2" className="textPadding" >“I founded TIWIT.life after my family experience with cord blood banking left something to be desired. I want to make your decision as super-informed, transparent and valuable as possible which was hard before TIWIT Life. We are here to solve all your confusion about umbilical cord blood banking and help you connect with best experts from the industry. We primarily focus on the customer experience at every level and customer service that people should expect in such an important partnership”</Typography>
                        </Grid>
                    </Grid>
                    <Grid item container  xs={12} md={6} lg={5} >
                        <YoutubeModal link="https://www.youtube.com/embed/HSgCEVomFn0"/>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
        </motion.div></div>
        </>
    )
}

export default GynecDoctor
