import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import {Menu, MenuItem,Button} from '@material-ui/core';
import { Link } from "react-router-dom"
const StyledMenu = withStyles({
  paper: {
    border: '1px solid #d3d4d5',
  },
})((props) => (
  <Menu
    elevation={0}
    getContentAnchorEl={null}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'center',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'center',
    }}
    {...props}
  />
));


export default function DropDownBtnServices({ id, name, click, link1, link2, link3 }) {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick1 = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  return (
    <div>
      <Button
        component={Link}
        className="link menu1"
        aria-controls="customized-menu"
        aria-haspopup="true"
        variant="text"
        onClick={handleClick1}
        
        id={id}
        name={name}
        fullWidth
        to=""
      >
        Services
      </Button>
      <StyledMenu
        id="customized-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >

        <Link onClick={()=>{click(link1);handleClose();}} name={link1} className="dropDownLinks" to={link1}>
          <MenuItem>
          <span className="menuButtonOption">Stem Cell BankIng</span>
          </MenuItem>
        </Link>
        <Link onClick={()=>{click(link2);handleClose();}} name={link2} className="dropDownLinks" to={link2}>
          <MenuItem>
          <span className="menuButtonOption">Genome Mapping</span>
          </MenuItem>
        </Link>
        <Link onClick={()=>{click(link3);handleClose();}} name={link3} className="dropDownLinks" to={link3}>
          <MenuItem>
          <span className="menuButtonOption">Pregnancy Consulting</span>
          </MenuItem>
        </Link>
      </StyledMenu>
    </div>
  );
}
