import React, { Component } from "react";
import { connect } from "react-redux";
import Footer from "../../components/UI/landingPageFooter/landingPageFooter";
import {
  setFooterLink
} from "../../actions/footer/footer";

export class Footer_Con extends Component {
  render() {
    return (
      <Footer {...this.props} />
    );
  }
};

export const mapStateToProps = store => {
  return {
    footer: store.footer,
    device: store.screenSize.device
  };
};

export const mapDispatchToProps = dispatch => {
  return {
    setFooterLink: (payload) => {
      dispatch(setFooterLink(payload));
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Footer_Con);
