import React, { useEffect } from "react";
import { useAnimation, motion } from 'framer-motion/dist/framer-motion';
import inView, { useInView } from "react-intersection-observer";

import { Grid, Typography, Link } from '@material-ui/core'
import './../../../styles/landingPageHomeComponents.css'
import comp1 from './../../../images/cell division.gif'


const BioTechStore = ({ device }) => {
    const { ref, inView } = useInView(
        { threshold: .2 }
    );
    const animation = useAnimation();
    useEffect(() => {
        console.log("inview=", inView);
        if (inView) {
            animation.start({
                y: 0,
                scale: 1,
                opacity: 1,
                rotate : 0,
                transition: {
                    type: 'spring', duration: 1, bounce: 0.2
                }
            })
        } if (!inView) {
            animation.start({
                // x: '-100vw',
                // scale: 0,
                // rotate : -30,
                // opacity: 0,
            })
        }
    }, [inView]);
    return (
        <div ref={ref}>
            <motion.div
                initial={{ y: '100vw', opacity: 0, scale: 1 , rotate : 0, }}
                animate={animation}
                whileHover={{ scale: 1.02 }}>

                <Grid container justifyContent="center" className="pagePadding">
                    <Grid item xs={11}>
                        <Grid container justifyContent="space-between">

                            <Grid item xs={12} md={6} lg={6}>
                                <Typography className="headingNormal">Welcome To World’s First</Typography>
                                <Typography className="headingBig">Super Biotech Store</Typography>
                                <Grid className="textMargin">
                                    <Typography className="textPadding" >We are the #1 digital health aggregator from India to smarten up your personal biotech. We curate cell-based services to secure you and your family from the unexpected. The brand new branches of synthetic biology will grow to cure more cancers and corona using human cells. TIWIT.life unlocks a brave new cellular world for the super-informed who map their genome and save their baby’s stem cells with us.</Typography>
                                </Grid>
                                <Grid container justifyContent="center">
                                   
                                    <Link to="" className="linkBtn"> KNOW MORE</Link> 
                                    
                                   
                                </Grid>
                            </Grid>

                            <Grid item container xs={12} md={6} lg={5}>
                                <Grid container alignItems="center" justifyContent="center">
                                    <Grid item >
                                        <img src={comp1} alt="" width="120%" />
                                    </Grid>

                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>

            </motion.div></div>
    )
}

export default BioTechStore
