import React, { Component } from "react";
import { connect } from "react-redux";
import LandingPageAppBar from "../../components/UI/landingPageAppbar/landingPageAppbar";
import {
  setFooterLink
} from "../../actions/footer/footer";
import {setDevice} from './../../actions/screenSize/screenSize'

export class LandingPageAppBar_Con extends Component {
  render() {
    return (
      <LandingPageAppBar {...this.props} />
    );
  }
};
export const mapStateToProps = store => {
  return {
    footer: store.footer,
    device: store.screenSize.device
  };
};

export const mapDispatchToProps = dispatch => {
  return {
    setFooterLink: (payload) => {
      dispatch(setFooterLink(payload));
    },
    setDevice: (payload) => {
      dispatch(setDevice(payload));
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LandingPageAppBar_Con);
